import {EViewSize} from "../enum/e-view-size";
import {IVisualComponent, IViewSize} from "../interface/i-visual-component";
import {SystemApplication} from "../../core/system-application";
import {EventEmitter, IEventEmitterError} from "../../core/event-emitter";
import jquery = require("jquery");

export interface IViewSizeAttr {
	extra_small?: number
	, small?: number
	, medium?: number
	, large?: number
}

/**
 * Classe base de todos os componentes
 *
 * @export
 * @abstract
 * @class ABasicComponent
 * @implements {IVisualComponent}
 * @memberof module:component
 */
export abstract class ABasicComponent{
	/**
	* Obtém o elemento DOM do componente
	*/   
	public element: HTMLElement;
	
	public onBeforeDestroy: EventEmitter<{}> = new EventEmitter();

	protected _uid: number = 0;

	constructor(tagh: string, tagc: string = "") {
		this._uid = SystemApplication.getUid();
		this.element = document.createElement(tagh);
		jquery(this.element)
			.addClass("ABasicComponent")
			.html(tagc)
			.attr("id", this.getUID());
	}

	/**
	 * Obtém o código de identificação do componente
	 *
	 * @returns {string}
	 */
	getUID(): string {
		return "uid_" + this._uid;
	}

	/**
	 * Retorna o parent do elemento
	 *
	 * @returns {HTMLElement}
	 */
	getParent():HTMLElement{
		return jquery(this.element).parent[0];
	}

	/**
	 * Atualiza os atributos do elemento
	 *
	 * @param {Object} properties
	 * @returns {this}
	 */
	setAttributes(properties: { [s: string]: string|boolean|number}): this {
		jquery(this.element).attr(properties);
		return this;
	}

	/**
	 * Atualiza atributo do elemento
	 *
	 * @param {string} attribute
	 * @returns {this}
	 */
	setAttribute(attribute:string,value:string|boolean|number): this {
		jquery(this.element).attr(attribute,<string>value);
		return this;
	}

	/**
	 * Retorna o valor do atributo do elemento
	 *
	 * @param {string} properties
	 * @returns {string}
	 */
	getAttribute(properties: string): string {
		return jquery(this.element).attr(properties);
	}    

	/**
	 * Atualiza os estilos do componente
	 *
	 * @param {Object} properties
	 * @returns {this}
	 */
	setCssProperties(properties: { [s: string]: string|number|boolean }): this {
		jquery(this.element).css(properties);
		return this;
	}

	/**
	 * Verifica a existencia de uma classe
	 *
	 * @param {string} class_name
	 * @returns {boolean}
	 */
	hasStyleName(class_name: string):boolean{
		return jquery(this.element).hasClass(class_name);
	}

	/**
	 * Adiciona uma nova classe ao componente
	 *
	 * @param {string} class_names
	 * @returns {this}
	 */
	addStyleName(class_names: string): this {
		jquery(this.element).addClass(class_names);
		return this;
	}

	/**
	 * Remove a classe do componente
	 *
	 * @param {string} class_names
	 * @returns {this}
	 */
	removeStyleName(class_names: string): this {
		jquery(this.element).removeClass(class_names);
		return this;
	}

	/**
	 * Remove todos as classes de estilo e insere a classe especificada do parametro
	 *
	 * @param {string} class_name
	 * @returns {this}
	 */
	setStyleName(class_names: string): this {
		jquery(this.element).removeClass().addClass(class_names);
		return this;
	}

	private setSizeIndividual(size: number, view: EViewSize = EViewSize.MEDIUM): void {
		let $element = jquery(this.element);
		let current: string = this.getSizeDesc(view);

		let classes = $element
			.attr("class")
			.replace(new RegExp(`[^\w]col-${current}-\\d+`, 'g'), '');

		/*	
		$element
			.removeClass()
			.addClass(classes + ` col-${current}-${size}`);
		*/	
		$element
                .removeClass();
		// if(size > 0){
            $element.addClass(`${classes} col-${current}-${size}`);
        // }else{
           // $element.addClass(classes + ` hidden-${current}`);
        // }	
	}

	private getSizeDesc(view: EViewSize): string {
		let SIZEMAP:string[] = [];
		SIZEMAP[EViewSize.EXTRA_SMALL] = 'xs';
		SIZEMAP[EViewSize.SMALL] = 'sm';
		SIZEMAP[EViewSize.MEDIUM] = 'md';
		SIZEMAP[EViewSize.LARGE] = 'lg';
		return SIZEMAP[view];
	}

	private setOffSetIndividual(size: number, view: EViewSize = EViewSize.MEDIUM): void {
		let classes = jquery(this.element).attr("class");
		let _sizeChoose: string = this.getSizeDesc(view);
		let ns = classes + " col-" + _sizeChoose + "-offset-" + size;
		if (classes.indexOf("col-" + _sizeChoose + "-offset-") > -1) {
			let _reg: RegExp = new RegExp('col-(' + _sizeChoose + ')-offset-(\\d{1,2})', 'g');
			ns = classes.replace(_reg, "col-$1-offset-" + size);
		}
		jquery(this.element).removeClass().addClass(ns);
	}

	private sizesToArray(): EViewSize[] {
		let views: EViewSize[] = [];
		views.push(EViewSize.EXTRA_SMALL);
		views.push(EViewSize.SMALL);
		views.push(EViewSize.MEDIUM);
		views.push(EViewSize.LARGE);
		return views;
	}

	/**
	 * Informa qual o tamanho do componente
	 *
	 * @param {IViewSizeAttr} sizes
	 * @returns {this}
	 */
	addSize(sizes: IViewSizeAttr): this {
		for (let prop in sizes) {
			this.setSizeIndividual(sizes[prop], <any>EViewSize[prop.toUpperCase()]);
		};
		return this;
	}

	/**
	 * Informa qual é o deslocamento horizontal a partir do ultimo elemento
	 *
	 * @param {IViewSizeAttr} sizes
	 * @returns {this}
	 */
	addOffSet(sizes: IViewSizeAttr): this {
		for (let prop in sizes) {
			//console.log(prop+" : "+sizes[prop]+" sz: "+EViewSize[prop.toUpperCase()]);
			this.setOffSetIndividual(sizes[prop], <any>EViewSize[prop.toUpperCase()]);
		};
		return this;
	}

	/**
	 * Informa o tamanho do componente
	 *
	 * @param {number} size
	 * @param {...EViewSize[]} views
	 * @returns {this}
	 */
	setSize(size: number, ...views: EViewSize[]): this {
		if (views.length == 0) {
			views = this.sizesToArray();
		};
		views.forEach((_tmview: EViewSize) => this.setSizeIndividual(size, _tmview));
		return this;
	}

	/**
	 * Informa qual é o deslocamento horizontal a partir do ultimo elemento
	 *
	 * @param {number} size
	 * @param {...EViewSize[]} views
	 * @returns {this}
	 */
	setOffSet(size: number, ...views: EViewSize[]): this {
		if (views.length == 0) {
			views = this.sizesToArray();
		};
		views.forEach((_tmview: EViewSize) => this.setOffSetIndividual(size, _tmview));
		return this;
	}

	/**
	 * Informa se o elemento deve ou não ser exibido
	 *
	 * @param {boolean} show
	 * @returns {this}
	 */
	setVisible(show: boolean): this {
		if (show) {
			jquery(this.element).show();
		} else {
			jquery(this.element).hide();
		};
		return this;
	}

	/**
	 * Retorna `true` caso o elemento seja visível
	 *
	 * @returns {boolean}
	 */
	isVisible(): boolean {
		return jquery(this.element).is(":visible");
	}


	public destroy(): void {
		this.onBeforeDestroy.emit(null).done(
			() => {
				for (let key in this) {
					let getType = {};
					if (this[key] && !(getType.toString.call(this[key]) === '[object Function]')) {
						//console.error(`verify the attribute ${key}`);
						if ((<any>this[key]).destroy) {
							(<any>this[key]).destroy();
						};
						try {
							this[key] = null;
						} catch (error) {
							//console.error(`${key} is only a getter`);
						}
					};
				};
				//jquery(this.element).removeEvent();
				jquery(this.element).remove();
			}
		);
	}
}
